/* make mobile editing on gutenberg possible */
@media only screen and (max-width: 414px) {
  body.post-type-post .block-editor-editor-skeleton__content .editor-styles-wrapper,
body.post-type-page .block-editor-editor-skeleton__content .editor-styles-wrapper {
    max-width: 100vw;
    margin: 0px auto;
    width: 100%;
  }

  .block-editor-block-list__layout {
    max-width: 100%;
  }

  .wp-block {
    max-width: 100%;
  }

  .block-editor-block-list__layout .block-editor-block-list__block[data-align=wide],
.block-editor-block-list__layout .block-editor-block-list__block[data-align=full] {
    width: 100vw;
  }
}
/* Gutenberg blocks alignwide and alignfull */
@media only screen and (min-width: 768px) {
  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    margin-left: calc((-100vw + 100% + 116px) / 2);
    margin-right: calc((-100vw + 100% + 116px) / 2);
    width: calc(100vw - 116px);
    max-width: calc(100vw - 116px);
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    width: calc(100vw - 280px);
    max-width: calc(100vw - 280px);
    margin-left: calc((-100vw + 100% + 280px) / 2);
    margin-right: calc((-100vw + 100% + 280px) / 2);
  }

  .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    margin-left: -10%;
    margin-right: -10%;
    width: 120%;
    max-width: 100vw;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media only screen and (min-width: 992px) {
  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
@media only screen and (min-width: 1200px) {
  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    max-width: calc(100vw - 280px - 72px);
    margin-left: -58px;
    margin-right: -58px;
  }
}
@media only screen and (min-width: 1500px) {
  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 120%;
    max-width: 1368px;
    margin-left: auto;
    margin-right: auto;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=wide] {
    width: 100%;
    margin-left: 0px;
    margin-right: 0px;
  }

  .edit-post-layout:not(.is-sidebar-opened) .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    width: calc(100vw - 116px);
    margin-left: -58px;
    margin-right: -58px;
  }

  .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper .wp-block[data-align=full] {
    max-width: calc(100vw - 280px);
    width: calc(100% + 116px);
  }
}
